<template>
  <div>
    <!--<remote-js src="/static/js/jquery-1.8.3.min.js"></remote-js>-->
    <!--<remote-js src="/static/js/jquery.easing.1.3.js"></remote-js>-->
    <!--<remote-js src="/static/js/prettyphoto/jquery.prettyPhoto.js"></remote-js>-->
    <!--<remote-js src="/static/js/jflickrfeed.js"></remote-js>-->
    <!--<remote-js src="/static/js/jquery.liveSearch.js"></remote-js>-->
    <!--<remote-js src="/static/js/jquery.form.js"></remote-js>-->
    <!--<remote-js src="/static/js/jquery.validate.min.js"></remote-js>-->
    <!--<remote-js src="/static/js/custom.js"></remote-js>-->
    <div class="header-wrapper">
      <header>
        <div class="container">
          <div class="logo-container">
            <!-- Website Logo -->
            <router-link to="/"  title="">
              <img src="../../static/images/logo.png" alt="">
            </router-link>
            <span class="tag-line"></span>
          </div>
          <!-- Start of Main Navigation -->
          <nav class="main-nav">
            <div class="menu-top-menu-container">
              <ul id="menu-top-menu" class="clearfix">
                <!--<li><router-link to="/article_list/"></router-link></li>-->
                <li><a @click="search"></a></li>
                <!--<li><a href="#"></a>-->
                  <!--<ul class="sub-menu">-->
                    <!--<li><a href="full-width.html">Python</a></li>-->
                    <!--<li><a href="elements.html">Redis</a></li>-->
                    <!--<li><a href="page.html">MongoDB</a></li>-->
                    <!--<li><a href="page.html">Linux</a></li>-->
                  <!--</ul>-->
                <!--</li>-->
                <li><router-link to='/contact/'></router-link></li>
                <!--<li><router-link to='/back/index/'></router-link></li>-->
              </ul>
            </div>
          </nav>
        </div>
      </header>
    </div>
    <div class="search-area-wrapper">
      <div class="search-area container">
        <h3 class="search-header">?</h3>
        <p class="search-tag-line">!</p>

        <div class="search-form clearfix">
          <input class="search-term required" type="text" v-model="wd" placeholder="" />
          <input class="search-btn" type="text" @click="search" value="" :class="search_btn"/>
          <div id="search-error-container"></div>
        </div>
      </div>
    </div>
    <remote-js></remote-js>
  </div>
</template>

<script>
export default {
  name: 'common',
  components: {
    'remote-js': {
      render (createElement) {
        return createElement(
          'script',
          {
            attrs: {
              type: 'text/javascript',
              src: this.src
            }
          }
        )
      }
    }
  },
  data () {
    return {
      wd: '',
      search_btn: 'search_btn'
    }
  },
  methods: {
    search: function () {
      // 
      // 
      this.$router.replace({'path': '/blank/', query: {'wd': this.wd, 'page': '1'}})
      // 
      // this.$router.push({'path': '/article_list/', query: {'wd': this.wd}})
    }
  }
}
</script>
<!--scoped-->
<style>
